import React from 'react'
import {NavBar,Icon,TabBar} from 'antd-mobile'
import Mytab2 from './Mytab2'

const Details = (props) => {
    
    return (
        <div>
        <NavBar mode="light" 
        icon={<Icon type="left" />} 
        onLeftClick={() => props.history.push('/home')}
        rightContent={
        <button style={{border:0,background:'white'}} onClick={()=>{props.history.push('/home')}}>取消</button>
       }>宝贝详情
       </NavBar>
      
       <img style={{width:'100%'}} src='https://img.alicdn.com/imgextra/i1/TB1jDYyMXXXXXXrapXXXXXXXXXX_!!0-item_pic.jpg_430x430q90.jpg'/>
       <Mytab2/>
   </div>
    )
}
    
export default Details
