import React from 'react'
import {connect} from 'react-redux'
const todos = (props) => {
    console.log(props)
    return (
        <div>
            {props.mydata.map((item)=><li>{item}</li>)}
            <button onClick={()=>props.dispatch({
                type:'add_todo',
                value:'1000'
            })}>添加</button>
        </div>
    )
}

export default connect((state)=>{
    return{
        mydata:state
    }
})(todos)
