import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        if(pathname.split('/').length>2){
            return null
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>
                                              
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu1'></i>                          
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>
			
                             
                        }
                        selectedIcon={
                            <i className='iconfont icon-ai66'></i>                             
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
          
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan_huaban'></i>
                        }
                        title="订单"
                        key="dingdan"
                        selected={ pathname === '/dingdan' }
                        onPress={ () => {
                            this.props.history.push('/dingdan')
                        } }
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode1'></i>
              
                       }
                        selectedIcon={  <i className='iconfont icon-wode2'></i>}
                        title="我的淘宝"
                        key="mytb"
                        selected={ pathname === '/mytb' }
                        onPress={ () => {
                            this.props.history.push('/mytb')
                        } }
                    >
                        
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);