import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="shop"
                        icon={
                            <i className='iconfont icon-dianpu1'></i>
                                              
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu'></i>                          
                        }
                        selected={ pathname === '/shop' }
                        onPress={ () => {
                            this.props.history.push('/shop');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu1'></i>
			
                             
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu'></i>                             
                        }
                        title="客服"
                        key="service"
                        // selected={ pathname === '/cart' }
                        // onPress={ () => {
                        //     this.props.history.push('/cart')
                        // } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
          
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan_huaban'></i>
                        }
                        title="收藏"
                        key="collect"
                        // selected={ pathname === '/dingdan' }
                        // onPress={ () => {
                        //     this.props.history.push('/dingdan')
                        // } }
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={<button style={{width:'100px',height:'35px',background:'#ffaa00',border:0,borderRadius:'50px 0 0 50px',color:'white',fontWeight:'bolder'}}>加入购物车</button>
                       }                     
                    >
			</TabBar.Item>
            <TabBar.Item
                        icon={<button style={{width:'100px',height:'35px',background:'#ff5000',border:0,borderRadius:'0 50px 50px 0',color:'white',fontWeight:'bolder'}}>立即购买</button>     
                       }                        
                    >
			</TabBar.Item>
            
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab2);